body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-image: url('../assets/fond.png');
}

.adminBody {
  color: white;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form {
  width: 30%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border : 1px solid white;
  border-radius: 15px;
  padding : 20px

}
.adminRow {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 10px 0;
}

.formRow{
  display: flex;
  justify-content: space-around;
  width: 100%;
  padding: 20px;
}


.mainContainer {
  width: 100%;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background-image: url(../assets/fond.png);
}

.adminForm {
  /* position: absolute; */
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  width: 70vw;
  height: 100%;
}

textarea,
select,
input {
  border-radius: 5px;
  outline: none;
  padding: 5px;
}

.Adminbody div {
  padding-bottom: 15px;
  padding-top: 15px;
  width: 100%;
  height: 100%;
}

.adminForm p {
  display: inline;
}

#root {
  width: 100%;
  padding-top: 0;
  padding-bottom: 0;
  height: 100%;
}

label {
  padding-top: 15px;
  padding-bottom: 10px;
}

button {
  border-radius: 5px;
  border: none;
  padding: 10px;
  font-family: 'Bree Serif', serif;
  outline: none;
  box-shadow: 5px 5px rgb(34, 7, 5);
}

button:hover {
  background-color: rgb(83, 249, 255);
}

button,
select {
  color: rgb(51, 26, 6);
}

h2{
  margin: 0;
  margin-top: 1em;
  display: flex;
  align-items: center;
}

h2::after {
  content: '';
  flex: 1;
  margin-left: 1rem;
  height: 1px;
  background-color: rgb(255, 255, 255);
}

.sousForm {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 80%;
  align-items: center;
  border: 1px solid white;
  border-radius: 15px;
  margin: 2%;
  padding: 2%;
}

.adminForm label {
  margin: 1%;
}

.adminForm button {
  margin: 1%;
}

.adminForm input {
  margin: 1%;
}

.require:invalid {
  border: 3px solid rgb(57, 234, 234);
  transition: 0.2s;
}

.require:valid {
  border: 1px solid black;
  transition: 0.2s;
}

.require:focus {
  border: 1px solid black;
}

textarea {
  resize: none;
}

.keywords {
  display: flex;
  flex-direction: row;
  /* justify-content: space-between; */
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
  padding-bottom: 50px;
}

.divKeywords {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 50%;
}

.returnButton {
  position: fixed;
  left: 3vw;
}

table {
  border: 1px solid white;
  border-radius: 15px;
  padding: 50px 100px 50px 100px;
}

td {
  padding: 20px;
}

.newTrail {
  width: 50%;
  border: 1px solid white;
  border-radius: 15px;
}
